"use client";

import { useState } from "react";
import Loading from "./Loading";


function Onboarding() {
  const [loading, setLoading] = useState(true)

  if(loading) {
    return (
      <Loading/>
    )
  }
  return (
    <div>
      <div className="curved-section"></div>
    </div>
   
  );
}

export default Onboarding;